.page-container {
    background: linear-gradient( rgb(250, 245, 227),rgb(255, 255, 255));
}

.tampoon {
    color: transparent;
}

.content-container-fruitslegumes {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: #FFF;
    width: 80%;
    margin: auto;
    border-radius: 25px;
    margin-bottom: 120px;
    height: flex;
    margin-top: 120px;
}

.balise-p-fruitslegumes {
  margin: auto;
  margin-top: 40px;
  max-width: 60%;
  font-size: 20px;
  font-family: 'Gill Sans', sans-serif;
}

.fruitslegumes-paragraph{
  text-align: center;
  font-family: 'Gill Sans', sans-serif;
  border-radius: 15px;
  margin-top: 50px;
  margin-bottom: 100px;
  padding: 35px;
  
}

.fruitslegumes-img-container img{
    text-align: center;
    padding: 40px;
    margin: 0 10px;
    flex: 1 1 auto;
    max-width: 100%;
    height: auto;
}
.title-fruitslegumes {
    margin-top: 200px;
    margin-bottom: 150px;
    color: #1A886E;
    border-radius: 10px;
    font-family: 'Gill Sans', sans-serif;
    border-bottom: solid #1A886E;
    border-top: solid #1A886E;
    border-left: solid #1A886E;
    border-right: solid #1A886E;
    padding: 30px;
    max-width: 25%;
    text-align: center;
    margin: auto;
    text-align: center;
    margin-top: 120px;
    margin-bottom: 120px;
}

.text-fruitslegumes {
    margin-top: 60px;
    text-align: center;
    padding: 40px;
}

  @media only screen and (max-width: 700px) {
    .fruitslegumes-img-container  {
      width: 49.99999%;
      margin: 6px 0;
    }
  }
  
  @media only screen and (max-width: 500px) {
    .fruitslegumes-img-container  {
      width: 100%;
    }
  }

  @media (max-width: 500px)  {
    .balise-p-fruitslegumes {
      max-width: 100%;
      font-size: 16px;
    }
    }

    @media (max-width: 500px)  {
      .title-fruitslegumes {
        max-width: 100%;
        font-size: 20px;
      }
      }