.vins-liens {
    color: black;
    text-decoration: none;
    padding: 0.5rem 1rem;
    font-size: 20px;
    color: brown;
    border-bottom: solid brown;
    margin: auto;
    }

.vins-li {
    display: inline;
    padding: 1rem;
}

.vins-container{
    margin-top: 40px;
}

.vins-liens:hover {
    color: white;
    }

.vins-liens:hover {
    background-color: brown;
    opacity: 0,2;
    border-radius: 4px;
    transition: all 0.2s ease-out;
    }

.vins-liens-mobile {
    display: none;
    }

.vins-container{
    margin: auto;
    display: table;
    width: 100%;
    text-align: center;
}

.vins-main-title {
    padding: 30px;
    font-family: 'Gill sans, sans retif';
    color: brown;
    border-top: solid brown;
    border-bottom: solid brown;
    max-width: 25%;
    margin: auto;
    margin-top: 40px;
    text-align: center;
    position: flex;
    margin-bottom: 100px;
}

.vins-title {
    padding: 30px;
    font-family: 'Gill sans, sans retif';
    color: brown;
    position: center;
    border-bottom: solid brown ;
    max-width: 23%;
    height: 70px;
    margin: auto;
    text-align: center;
}

@media (max-width: 700px)  {
    .vins-liens{
        max-width: 100%;
        font-size: 16px;
        padding: 10px;
        display: flex;
        text-align: center ;
    }
}

@media (max-width: 500px)  {
    .vins-main-title {
        max-width: 100%;
        font-size: 18px;
    }
}
@media (max-width: 500px)  {
    .vins-title {
        max-width: 100%;
        font-size: 18px;
    }
}