.text-title-contact {
    margin: auto;
    margin-top: 120px;
    margin-bottom: 100px;
    color: #1A886E;
    border-radius: 10px;
    font-family: 'Gill Sans', sans-serif;
    border-bottom: solid #1A886E;
    border-top: solid #1A886E;
    border-left: solid #1A886E;
    border-right: solid #1A886E;
    padding: 30px;
    max-width: 25%;
    text-align: center;
}

.contact-img-container img{
    text-align: left;
    padding: 40px;
    margin: 0 10px;
    flex: 1 1 auto;
    max-width: 100%;
    height: auto;
}


.contact-list{
    text-align: center;
    border-radius: 15px;
    padding: 15px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: #FFF;
    max-width: 80%;
    margin: auto;
    border-radius: 25px;
    margin-bottom: 150px;
    margin-top: 80px;
    height: 200px;
}

.icon-svg {
    font-size: 20px;
    margin-left: auto;
    padding: 10px;
    max-width: 70%;
    display: block;
    text-align: left;
}

.page-container-contact {
    background: linear-gradient( rgb(250, 245, 227),rgb(255, 255, 255)) ;
}

.tampoon {
    color: transparent;
    background: rgb(250, 245, 227);
}

.content-container-contact {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: #FFF;
    width: 80%;
    margin: auto;
    border-radius: 25px;
    margin-bottom: 150px;
    height: flex;
    padding: 50px;
    margin: auto;
    font-size: 20px;
    font-family: 'Gill Sans', sans-serif;
}

.content-container-contact {
    margin: auto;
}


@media (max-width: 700px) {
    .icon-svg {
        font-size: 15px;
        max-width: 100%;
        margin: auto;
        text-align: center;
        padding: 0px;
        margin-top: 10px;
    }
    .contact-list {
        display: block;
    }
    .contact-img-container {
        width: auto;
    }
    .contact-img-container img {
        max-width: 400px;
        margin-left: -76px;
    }
    .text-title-contact {
        max-width: 100%;
        font-size: 19px;
    }
}