.labelContainer {
    display: inline-flex;
    margin-left: flex;
    font-size: 17px;
    color: rgb(92, 91, 91);
    width: 100%;
    margin-top: 20px;
}

.plantlogo {
    text-align: center;
    width: 33%;
    margin: auto;
}

.biologo {
    text-align: center;
    width: 33%;
    margin: auto;
}

.earthlogo {
    text-align: center;
    width: 33%;
    margin: auto;
}

.plantlogo img{
    width: 130px;
    height: auto;
    margin: auto;
}

.biologo img{
    width: 130px;
    height: auto;
    margin: auto;
}

.earthlogo img{
    width: 130px;
    height: auto;
    margin: auto;
}

@media (max-width:700px) {
    .labelContainer {
        font-size: 15px;
        display: inline-block;
    }
    .earthlogo img{
        width: 90px;
    }
    .biologo img{
        width: 90px;
    }
    .plantlogo img{
        width: 90px;
    }

    .plantlogo {
        margin-top: 30px;
        width: 100%;
        
    }
    .earthlogo {
        margin-top: 30px;
        width: 100%;
    }
    .biologo {
        margin-top: 30px;
        width: 100%;
    }
}